import { Component, OnInit } from '@angular/core';
import {FormsModule} from '@angular/forms';

import {Providers} from '../providers.interface';
import {ProvidersService} from '../providers.service';

@Component({
  selector: 'app-provider-register-page',
  templateUrl: './provider-register-page.component.html',
  styleUrls: ['./provider-register-page.component.css']
})
export class ProviderRegisterPageComponent implements OnInit {

provider: Providers  = { companyName: '', FirstName: '', LastName: '', UserName: '', Password: '', Category: '',

  Description: '', Area: '', StreetName: '', StreetNumber: 0, TelNumber: '', Mail: '', Iban: '' };

  constructor(private providerService: ProvidersService) { }

  ngOnInit() {
  }
  onSubmit() {
    this.providerService.createProvider(this.provider).subscribe(
      value => {
        console.log('[POST] create Parent successfully', value);
      },
      () => {
        console.log('POST Parent - now completed.');
      });
  }

}



